body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.syren__container{
  border: 4px solid #22BBBB;
  border-radius: 9px;
  max-width:500px;
  margin:20px auto;
}
.dial__container{
  display: flex;
  flex-direction: row;
}
.dial__slot {
  margin: 0 20px;
  max-width: 60px;
}

.trigger__slot{
  margin: 20px;
}
.button__container {
  margin-left: -15px;
  margin-bottom: 41px; 
}